:root
{
  --yellow: #F5C32C;
    --orange : #FCA61F;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: var(--black); 
    background-color: #000;  
    transition: .7s; 
}
.dark-mode{
  background-color: white !important;
}
.for-icons-container{
  /* position: relative; */
  display: flex;
  flex-direction: column;
}
#massage-app{
  position: fixed;
  top: 87vh;
  right: 6.4%;
  z-index: 103;
  /* background-color: #F5C32C; */
}
#scroll-icons{
  position: fixed;
  top: 75vh;
  left: 4%;
  z-index: 100;
}
@media screen and (max-width: 1050px){
  .App{
    padding: 0.5rem .9rem;
    }
    p{
      font-size: .5rem;
    }
    #massage-app{
      position: fixed;
      top: 90vh;
     
      /* background-color: #F5C32C; */
    }
    #scroll-icons{
      left: 1.4%;
    }

}

