  .skill-bar {
    margin-bottom: 20px;
  }
  
  .bar {
    background-color: #ff0000;
    height: 15px;
    border-radius: 8px;
    overflow: hidden;
  }
  
  .fill {
    background-color: #4caf50;
    height: 100%;
    /* width: 0; */
    transition: width 1s ease-in-out; /* Animation transition */
  }
  .bar.animate .fill {
    width: 100%;
  }
  
  .fill {
    /* ... */
    width: 0; /* Remove this line */
    transition: width 1s ease-in-out;
  }
